import * as Yup from 'yup';
import { useState } from 'react';
import { useSnackbar } from 'notistack';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
// @mui
import { Stack, Card } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// components
import { FormProvider, RHFTextField } from '../../../../components/hook-form';
// user
import useAuth from '../../../../hooks/useAuth';
import axios from '../../../../utils/axios';

// ----------------------------------------------------------------------

export default function AccountChangePin() {
  const { enqueueSnackbar } = useSnackbar();
  const { user } = useAuth();
  const [pin, Setpin] = useState(user?.pin === null || user?.pin === '');
  const AccessToken = window.localStorage.getItem('accessToken');
  const ChangePassWordSchema = Yup.object().shape({
    oldpin: Yup.string().required('Old Transaction Pin is required'),
    newpin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
    confirmpin: Yup.string().oneOf([Yup.ref('newpin'), null], 'Transaction Pin must match'),
  });
  const newTransaction = Yup.object().shape({
    newpin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
    confirmpin: Yup.string().oneOf([Yup.ref('newpin'), null], 'Transaction Pin must match'),
  });
  const defaultValues = {
    oldpin: '',
    newpin: '',
    confirmpin: '',
  };

  const methods = useForm({
    resolver: yupResolver(!pin ? ChangePassWordSchema : newTransaction),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/change/pin/by/user/habukhan/${AccessToken}/now`, data)
      reset();
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  };

  const createpin = async (data) => {
    try {
      await axios.post(`/api/create/newpin/by/user/system/${AccessToken}/now`, data)
      reset();
      Setpin(false)
      enqueueSnackbar('create success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  return (
    <Card sx={{ p: 3 }}>
      <FormProvider methods={methods} onSubmit={handleSubmit(!pin ? onSubmit : createpin)}>
        <Stack spacing={3} alignItems="flex-end">
          {!pin && <RHFTextField name="oldpin" type="number" label="Old Transaction Pin" />}

          <RHFTextField name="newpin" type="number" label="New Transaction Pin" />

          <RHFTextField name="confirmpin" type="number" label="Confirm Transaction Pin" />
          <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
            Save Changes
          </LoadingButton>
        </Stack>
      </FormProvider>
    </Card>
  );
}
