import * as Yup from 'yup';
import { useState, useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';

// @mui
import { Box, Grid, Card, Typography, Stack, alpha, styled, Divider } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// components
import { FormProvider, RHFTextField, RHFSelect } from '../../../../components/hook-form';
import Iconify from '../../../../components/Iconify';
import Label from '../../../../components/Label';
// ----------------------------------------------------------------------
import axios from '../../../../utils/axios';

// Styled Components
const StyledCard = styled(Card)(({ theme }) => ({
  boxShadow: theme.customShadows.z8,
  transition: 'all 0.3s ease-in-out',
  '&:hover': {
    boxShadow: theme.customShadows.z16,
  },
}));

const BankCard = styled(Card)(({ theme }) => ({
  background: `linear-gradient(135deg, ${theme.palette.primary.dark} 0%, ${theme.palette.primary.main} 100%)`,
  color: theme.palette.common.white,
  padding: theme.spacing(4),
  borderRadius: theme.shape.borderRadius * 2,
  position: 'relative',
  overflow: 'hidden',
  minHeight: 200,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'space-between',
  '&::before': {
    content: '""',
    position: 'absolute',
    top: -50,
    right: -50,
    width: 200,
    height: 200,
    background: `radial-gradient(circle, ${alpha(theme.palette.common.white, 0.1)} 0%, transparent 70%)`,
    borderRadius: '50%',
  },
}));

export default function AccountBilling() {
  const [account, SetAccount] = useState('')
  const [code, Setcode] = useState('');
  const [bank, SetBank] = useState([]);
  const [load, SetLoad] = useState();
  const [bankname, Setbankname] = useState('');
  const { enqueueSnackbar } = useSnackbar();

  const AccessToken = window.localStorage.getItem('accessToken');
  const UpdateUserSchema = Yup.object().shape({
    account_number: Yup.string().required('Account Number required'),
  });

  const defaultValues = useMemo(
    () => ({
      account_name: account?.bank_name,
      account_number: account?.account_number,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [account]
  );

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
  });
  useEffect(() => {
    SetLoad(true);
    axios.get('https://api.paystack.co/bank').then(data => { SetBank(data?.data?.data); SetLoad(false); })
    axios.get(`/api/user/accountdetails/wallet/${AccessToken}/secure/habukhan`).then(data => {
      SetAccount(data.data?.bank);
      SetLoad(false);
    });

  }, [AccessToken]);
  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (account) {
      reset(defaultValues);
      Setbankname(account?.bank);
      Setcode(account?.bank_code);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account]);
  const values = watch();
  const onSubmit = async (data) => {
    try {

      await axios.post(`/api/accountdetails/habukhan/secure/${AccessToken}/secure`, {
        account_name: data.account_name,
        bank_name: bankname,
        bank_code: code,
        account_number: data.account_number
      });
      axios.get(`/api/user/accountdetails/wallet/${AccessToken}/secure/habukhan`).then(data => {
        SetAccount(data.data?.bank);
      });
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  };

  const bankchange = async (data) => {
    Setbankname(data)
    const value = bank.filter((item) => item.name === data)
    if (value[0]) {
      Setcode(value[0].code)
    }
  }

  const maskAccountNumber = (number) => {
    if (!number) return '****';
    const str = number.toString();
    return '****' + str.slice(-4);
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={5}>
          <Stack spacing={3}>
            {/* Visual Bank Card */}
            {account && (
              <BankCard>
                <Box>
                  <Stack direction="row" alignItems="center" spacing={1} sx={{ mb: 2 }}>
                    <Iconify icon="eva:credit-card-outline" width={28} height={28} />
                    <Typography variant="overline" sx={{ opacity: 0.8, letterSpacing: 1.5 }}>
                      Bank Account
                    </Typography>
                  </Stack>
                  <Typography variant="h4" sx={{ fontFamily: 'monospace', letterSpacing: 2, mb: 1 }}>
                    {maskAccountNumber(account?.account_number)}
                  </Typography>
                </Box>

                <Box>
                  <Typography variant="caption" sx={{ opacity: 0.7, display: 'block', mb: 0.5 }}>
                    ACCOUNT HOLDER
                  </Typography>
                  <Typography variant="h6" sx={{ mb: 2 }}>
                    {account?.bank_name || 'Not Set'}
                  </Typography>

                  <Stack direction="row" justifyContent="space-between" alignItems="center">
                    <Box>
                      <Typography variant="caption" sx={{ opacity: 0.7, display: 'block' }}>
                        BANK
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: 600 }}>
                        {account?.bank || 'N/A'}
                      </Typography>
                    </Box>
                    <Label color="success" sx={{ px: 2, py: 1 }}>
                      <Stack direction="row" spacing={0.5} alignItems="center">
                        <Iconify icon="eva:checkmark-circle-2-fill" width={16} />
                        <span>Verified</span>
                      </Stack>
                    </Label>
                  </Stack>
                </Box>
              </BankCard>
            )}

            {/* Security Badge */}
            <Card sx={{ p: 2.5, bgcolor: 'background.neutral' }}>
              <Stack direction="row" spacing={2} alignItems="center">
                <Box
                  sx={{
                    width: 48,
                    height: 48,
                    borderRadius: '50%',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    bgcolor: (theme) => alpha(theme.palette.success.main, 0.16),
                  }}
                >
                  <Iconify icon="eva:shield-outline" width={24} color="success.main" />
                </Box>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                    Secured with Encryption
                  </Typography>
                  <Typography variant="caption" sx={{ color: 'text.secondary' }}>
                    Your banking information is protected with bank-level security
                  </Typography>
                </Box>
              </Stack>
            </Card>
          </Stack>
        </Grid>

        <Grid item xs={12} md={7}>
          <Stack spacing={3}>
            <StyledCard sx={{ p: 4 }}>
              <Typography variant="h5" sx={{ mb: 3, color: 'primary.darker', fontWeight: 700 }}>
                Account Details
              </Typography>

              {!load ? (
                <>
                  <Stack spacing={3}>
                    <RHFSelect
                      name="bank_name"
                      label="Bank Name"
                      onChange={(data) => bankchange(data.target.value)}
                      value={bankname}
                    >
                      <option value="" />
                      {bank.map((option) => (
                        <option key={option?.code} value={option?.name}>
                          {option?.name}
                        </option>
                      ))}
                    </RHFSelect>

                    <RHFTextField
                      name="account_number"
                      label="Account Number"
                      InputProps={{
                        startAdornment: <Iconify icon="eva:hash-outline" width={20} sx={{ mr: 1, color: 'text.secondary' }} />,
                      }}
                    />

                    {values?.account_name !== undefined && (
                      <RHFTextField
                        name="account_name"
                        label="Account Name"
                        disabled
                        InputProps={{
                          startAdornment: <Iconify icon="eva:person-outline" width={20} sx={{ mr: 1, color: 'text.disabled' }} />,
                        }}
                      />
                    )}
                  </Stack>

                  <Divider sx={{ my: 3 }} />

                  <Stack direction="row" justifyContent="flex-end">
                    <LoadingButton
                      type="submit"
                      variant="contained"
                      loading={isSubmitting}
                      size="large"
                      sx={{
                        minWidth: 160,
                        background: (theme) => `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.light} 100%)`,
                        '&:hover': {
                          background: (theme) => `linear-gradient(135deg, ${theme.palette.primary.dark} 0%, ${theme.palette.primary.main} 100%)`,
                        },
                      }}
                    >
                      Save Changes
                    </LoadingButton>
                  </Stack>
                </>
              ) : (
                <Box sx={{ textAlign: 'center', py: 4 }}>
                  <Typography variant="h6" sx={{ color: 'text.secondary' }}>
                    Loading...
                  </Typography>
                </Box>
              )}
            </StyledCard>
          </Stack>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
