import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { useCallback } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { Box, Grid, Card, Stack, Typography, Divider, alpha, styled } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// hooks
import useAuth from '../../../../hooks/useAuth';
// utils
import { fData } from '../../../../utils/formatNumber';
// components
import Label from '../../../../components/Label';
import Iconify from '../../../../components/Iconify';
import axios from '../../../../utils/axios';
// components
import { FormProvider, RHFTextField, RHFUploadAvatar } from '../../../../components/hook-form';
import SettingMode from '../../../../components/settings/SettingMode';
import SettingFullscreen from '../../../../components/settings/SettingFullscreen';

// ----------------------------------------------------------------------

// Styled Components
const StyledCard = styled(Card)(({ theme }) => ({
  boxShadow: theme.customShadows.z8,
  transition: 'all 0.3s ease-in-out',
  '&:hover': {
    boxShadow: theme.customShadows.z16,
  },
}));

const StatCard = styled(Card)(({ theme }) => ({
  padding: theme.spacing(2),
  textAlign: 'center',
  background: `linear-gradient(135deg, ${alpha(theme.palette.primary.lighter, 0.8)} 0%, ${alpha(theme.palette.primary.lighter, 0.4)} 100%)`,
  border: `1px solid ${alpha(theme.palette.primary.main, 0.08)}`,
  transition: 'all 0.2s ease-in-out',
  '&:hover': {
    transform: 'translateY(-4px)',
    boxShadow: `0 12px 24px ${alpha(theme.palette.primary.main, 0.12)}`,
    borderColor: theme.palette.primary.light,
  },
}));

export default function AccountGeneral() {
  const { enqueueSnackbar } = useSnackbar();
  const { user } = useAuth();
  const AccessToken = window.localStorage.getItem('accessToken');
  const UpdateUserSchema = Yup.object().shape({
    fullname: Yup.string().required('Name is required'),
  });


  const defaultValues = {
    fullname: user?.name || '',
    email: user?.email || '',
    profile_image: user?.profile_image || '',
    username: user?.username || '',
    phoneNumber: user?.phone || '',
    address: user?.address || '',
    webhook: user?.webhook || '',
    about: user?.about || '',
    type: user?.type || ''
  };

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    setValue,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const values = watch();
  const onSubmit = async (data) => {
    // eslint-disable-next-line prefer-const
    let formData = new FormData();
    // eslint-disable-next-line no-restricted-syntax, guard-for-in
    for (const key in data) {
      formData.append(key, data[key]);
    }
    try {
      await axios.post(`/api/edituser/habukhan/secure/${AccessToken}/secure`, formData)
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  };


  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          'profile_image',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Stack spacing={3}>
            {/* Profile Card */}
            <StyledCard sx={{ py: 6, px: 3, textAlign: 'center', position: 'relative' }}>
              <Label
                color={'success'}
                sx={{
                  textTransform: 'uppercase',
                  position: 'absolute',
                  top: 16,
                  right: 16,
                  fontWeight: 700,
                  px: 2,
                  py: 0.75,
                }}
              >
                {values.type}
              </Label>

              <RHFUploadAvatar
                name="profile_image"
                accept="image/*"
                maxSize={3145728}
                onDrop={handleDrop}
                sx={{
                  '& .MuiAvatar-root': {
                    width: 150,
                    height: 150,
                    border: (theme) => `4px solid ${alpha(theme.palette.primary.main, 0.16)}`,
                    transition: 'all 0.3s ease-in-out',
                    '&:hover': {
                      borderColor: 'primary.main',
                      transform: 'scale(1.05)',
                    },
                  },
                }}
                helperText={
                  <Typography
                    variant="caption"
                    sx={{
                      mt: 2,
                      mx: 'auto',
                      display: 'block',
                      textAlign: 'center',
                      color: 'text.secondary',
                    }}
                  >
                    Allowed *.jpeg, *.jpg, *.png, *.gif
                    <br /> max size of {fData(3145728)}
                  </Typography>
                }
              />

              <Typography variant="h6" sx={{ mt: 2, mb: 0.5 }}>
                {user?.name}
              </Typography>
              <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                @{user?.username}
              </Typography>

              <Divider sx={{ my: 3 }} />

              {/* Quick Settings */}
              <Stack spacing={1.5}>
                <SettingMode />
                <SettingFullscreen />
              </Stack>
            </StyledCard>

            {/* User Stats Cards */}
            <Stack spacing={2}>
              <StatCard>
                <Stack direction="row" alignItems="center" justifyContent="center" spacing={1} sx={{ mb: 1 }}>
                  <Iconify icon="eva:calendar-outline" width={20} height={20} color="primary.main" />
                  <Typography variant="caption" sx={{ color: 'text.secondary', fontWeight: 600 }}>
                    MEMBER SINCE
                  </Typography>
                </Stack>
                <Typography variant="h6" sx={{ color: 'primary.darker' }}>
                  {new Date(user?.created_at || new Date()).toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })}
                </Typography>
              </StatCard>

              <StatCard>
                <Stack direction="row" alignItems="center" justifyContent="center" spacing={1} sx={{ mb: 1 }}>
                  <Iconify icon="eva:clock-outline" width={20} height={20} color="primary.main" />
                  <Typography variant="caption" sx={{ color: 'text.secondary', fontWeight: 600 }}>
                    LAST LOGIN
                  </Typography>
                </Stack>
                <Typography variant="h6" sx={{ color: 'primary.darker' }}>
                  {new Date(user?.updated_at || new Date()).toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })}
                </Typography>
              </StatCard>
            </Stack>
          </Stack>
        </Grid>

        <Grid item xs={12} md={8}>
          <StyledCard sx={{ p: 4 }}>
            <Typography variant="h5" sx={{ mb: 3, color: 'primary.darker', fontWeight: 700 }}>
              Personal Information
            </Typography>

            <Box
              sx={{
                display: 'grid',
                rowGap: 3,
                columnGap: 2,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField
                name="fullname"
                label="Full Name"
                disabled
                InputProps={{
                  startAdornment: <Iconify icon="eva:person-outline" width={20} sx={{ mr: 1, color: 'text.disabled' }} />,
                }}
              />
              <RHFTextField
                name="username"
                label="Username"
                disabled
                InputProps={{
                  startAdornment: <Iconify icon="eva:at-outline" width={20} sx={{ mr: 1, color: 'text.disabled' }} />,
                }}
              />
              <RHFTextField
                name="email"
                label="Email Address"
                disabled
                InputProps={{
                  startAdornment: <Iconify icon="eva:email-outline" width={20} sx={{ mr: 1, color: 'text.disabled' }} />,
                }}
              />
              <RHFTextField
                name="phoneNumber"
                label="Phone Number"
                disabled
                InputProps={{
                  startAdornment: <Iconify icon="eva:phone-outline" width={20} sx={{ mr: 1, color: 'text.disabled' }} />,
                }}
              />

              <RHFTextField
                name="address"
                label="Address"
                InputProps={{
                  startAdornment: <Iconify icon="eva:pin-outline" width={20} sx={{ mr: 1, color: 'text.secondary' }} />,
                }}
              />
              <RHFTextField
                name="webhook"
                label="Webhook URL"
                InputProps={{
                  startAdornment: <Iconify icon="eva:link-2-outline" width={20} sx={{ mr: 1, color: 'text.secondary' }} />,
                }}
              />

            </Box>

            <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
              <RHFTextField
                name="about"
                multiline
                rows={4}
                label="About"
                InputProps={{
                  startAdornment: <Iconify icon="eva:file-text-outline" width={20} sx={{ mr: 1, mt: 1, color: 'text.secondary' }} />,
                }}
              />

              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
                size="large"
                sx={{
                  minWidth: 160,
                  background: (theme) => `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.light} 100%)`,
                  '&:hover': {
                    background: (theme) => `linear-gradient(135deg, ${theme.palette.primary.dark} 0%, ${theme.palette.primary.main} 100%)`,
                  },
                }}
              >
                Save Changes
              </LoadingButton>
            </Stack>
          </StyledCard>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
